.App {
  display: flex;
  justify-content: center;
  margin-top: 30vh;;
}
.pad-bank {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.drum-pad {
  width:100px;
  height:100px;
  border: 1px solid black;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(216, 103, 27, 0.7);
}
